/** @jsx React.DOM */
var React = require('react');

/* Utilities */
var moment = require('moment');

var MyOwnRunsRow = React.createClass({
  displayName: 'MyOwnQueriesRow',

  propTypes: {
    model: React.PropTypes.object.isRequired
  },

  componentWillReceiveProps: function(nextProps) {
    this.setState({ model: nextProps.model });
  },

  getInitialState: function() {
    return { model: this.props.model };
  },

  render: function () {
    return (
      React.createElement("tr", null, 
        React.createElement("td", null, this.state.model.query), 
        React.createElement("td", null, this.queryState()), 
        React.createElement("td", null, this.queryStartTime()), 
        React.createElement("td", null, this.queryEndTime()), 
        React.createElement("td", null, this.queryDownloadLink())
      )
    );
  },

  // - Presenter helpers
  queryState: function() {
    if ( !!this.state.model.state ) {
      if ( this.state.model.state === 'FAILED' ) {
        return (
          React.createElement("div", {className: "text-danger"}, 
            React.createElement("span", {className: "glyphicon glyphicon-info-sign"}), " ", this.state.model.error.message
          )
        );
      } else if ( this.state.model.state === 'FINISHED' ) {
        return (React.createElement("span", {className: "label label-success"}, this.state.model.state));
      } else {
        return (React.createElement("span", {className: "label label-info"}, this.state.model.state));
      }
    }
  },

  queryStartTime: function () {
    if ( !!this.state.model.queryStarted ) {
      return moment.utc(this.state.model.queryStarted, 'x').format('MM-DD-YYYY');
    }
  },

  queryEndTime: function() {
    if ( !!this.state.model.queryFinished ) {
      return moment.utc(this.state.model.queryFinished, 'x').calendar();
    }
  },

  queryDownloadLink: function() {
    if ( !!this.state.model.output && !!this.state.model.output.location ) {
      return (
        React.createElement("a", {href: this.state.model.output.location, title: "Grab the file"}, 
          "Download"
        )
      );
    }
  }
});

module.exports = MyOwnRunsRow;